export default {
	data() {
		return {
			list: []
		}
	},
	methods: {
		play(e){
			console.log(e.currentTarget.id);
			this.list.map(item=>{
				if(item.id != e.currentTarget.id){
					uni.createVideoContext(item.id).pause()
				}
			})
		},
		yu() {
			uni.request({
				url: `${this.$api}/index/movie/hot?qq=122212489&type=trailer`,
				method: 'POST',
				data: {},
				success: res => {
					this.list = res.data.data
					// console.log(this.list);
				},
				fail: () => {},
				complete: () => {}
			});
		}
	},
	mounted() {
		this.yu()
	}
}
